	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-03-30-lifetime-marriage/index.md absPath of file >>> MarkdownRemark","html":"<p>In church today an older lady stood up and said she was grateful for her marriage of 60 years. I was impressed. I think that the divorce today tends to discourage people from believing that they can spend a lifetime with another person. It seems rare to find a couple that has been together for 60 years not just because of divorce but also because of health. Whenever I see a couple that is celebrating half a century together that accomplishment feels far more significant than all the awards, the academic degrees, or political achievements one can accumulate.</p>","frontmatter":{"title":"Lifetime Marriage","date":"March 30, 2008","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{}}

